.range-filter__controls {
  background-color: @color-ecru-white;
  border-radius: 5px;
  padding: 39px 20px;
  margin-bottom: 9px;
}

.range-filter__scale {
  position: relative;
  height: 2px;
  background-color: @color-iron;
}

.range-filter__bar {
  height: 2px;
  background: @color-carribean-green;
}

.range-filter__toggle {
  position: absolute;
  top: -10px;
  padding: 2px;
  border: 8px solid @color-white;
  border-radius: 50%;
  background-color: @color-dark-gray;
  box-shadow: 0 2px 0 0 @color-third-dark-gray;
  cursor: pointer;
}

.range-filter__price-inputs {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.range-filter__input {
  box-sizing: border-box;
  width: 95px;
  font-size: 1.063em;
  line-height: 1.125rem;
  text-align: center;
  background-color: @color-ecru-white;
  border-width: 0;
  border-radius: 5px;
  padding: 10px 5px;
  appearance: textfield;
}

.range-filter__input::-webkit-inner-spin-button,
.range-filter__input::-webkit-outer-spin-button {
  appearance: none;
  margin: 0;
}
