.search-field {
  position: relative;
}

.search-field__icon {
  position: absolute;
  fill: transparent;
  stroke: white;
  top: 13px;
  left: 17px;
  opacity: 0.3;
}

.search-field:hover .search-field__icon {
  opacity: 1;
}

.search-field__input {
  width: 100%;
  height: 100%;
  font-family: "Inter", "Arial", sans-serif;
  font-size: 1.125em;
  font-weight: 300;
  line-height: 1.5rem;
  color: @color-white;
  border-width: 0;
  background-color: @color-search-field__input;
  padding-left: 42px;
  padding-right: 24px;
}

.search-field__input:focus {
  background-color: @color-white;
  color: @color-black;
}

.search-field__input:focus + .search-field__icon {
  opacity: 1;
  stroke: @color-fern-frond;
}

.search-field__input::placeholder {
  color: @color-white;
  opacity: 1;
}
