.product-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  box-sizing: border-box;
  border: 1px solid @color-gallery;
  padding-bottom: 20px;
  text-align: center;
  min-height: 318px;
}

.product-card__title {
  font-size: 1.125em;
  line-height: 1.25rem;
  font-family: "Lora", "Times", serif;
  font-weight: 500;
  margin-top: 10px;
  margin-bottom: 12px;
  max-width: 185px;

  a {
    color: @color-black;
  }

  a:hover, a:active {
    color: @color-fern-frond;
  }
}

.product-card__author {
  font-size: 14px;
  margin-bottom: 22px;
  color: @color-dark-gray;
}

.product-card__author:hover{
  color: @color-fern-frond;
}

.product-card__img-wrapper {
  min-height: 178px;
  order: -1;
  display: flex;
  align-items: center;
}

.product-card__old-price {
  display: block;
  font-size: 15px;
  line-height: 18px;
  color: @color-silver-chalice;
}

.product-card__price {
  display: block;
  font-size: 22px;
  color: @color-black;
  border-radius: 3px;
  // padding: 10px 45px 10px 32px;
  // align-self: flex-start;
  margin-top: 10px;
  // margin-left: 44px;
}

.product-card__action-buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  width: 135px;
  top: 100px;
  left: 40px;
  opacity: 0;

  &:focus-within {
    opacity: 1;
  }
}

.product-card:hover {
  box-shadow: 0 4px 20px @color-fifteen-black;

  .product-card__action-buttons {
    opacity: 1;
  }

  img {
    opacity: 0;
  }
}

.product-card__button {
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 18px;
  border-radius: 4px;
  width: 100%;
  border: none;
}

.product-card__button--buy {
  display: flex;
  // align-items: flex-start;
  color: @color-white;
  background-color: @color-apple;
  box-shadow: inset 0 -3px 0 @color-bilbao;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 15px;
  margin-bottom: 10px;
  cursor: pointer;

  &:hover {
    background-color: @color-lima;
  }

  &:active {
    background-color: @color-green-leaf;
  }

  .product-card__button-icon {
    fill: transparent;
    stroke: white;
  }
}

.product-card__button-icon {
  margin-right: 8px;
  opacity: 0.8;
}

.product-card__button--bookmark {
  display: flex;
  justify-content: center;
  color: @color-pickled-bluewood;
  background-color: @color-white;
  box-shadow: inset 0 0 0 3px @color-apple;
  padding-top: 11px;
  padding-bottom: 9px;
  cursor: pointer;

  .product-card__button-icon {
    fill: @color-apple;
    stroke: @color-apple;
  }

  &:hover {
    box-shadow: inset 0 0 0 3px @color-fern-frond;
    color: @color-fern-frond;
  }

  &:active {
    box-shadow: inset 0 0 0 3px @color-fern-frond;
    color: @color-fern-frond;
    opacity: 0.5;
  }
}
