}
.category-title {
  font-weight: 400;
  font-size: 1.875rem;
  line-height: 1;
  box-sizing: border-box;
  width: 940px;
  text-transform: uppercase;
  color: #32425c;
  background-color: #f1f5f7;
  padding-top: 29px;
  padding-bottom: 30px;
  padding-left: 29px;
  margin: 20px auto;
}
.check {
  position: relative;
  display: grid;
  grid-template-columns: 35px min-content;
  align-items: center;
  cursor: pointer;
  font-size: 0.938em;
  line-height: 1.188rem;
}
.check__input {
  position: absolute;
  appearance: none;
}
.check__icon {
  justify-self: flex-start;
  fill: #c5c5c5;
}
.check__input:hover + .check__icon,
.check__input:focus + .check__icon {
  fill: #B5B5B5;
}
.check__input:disabled + .check__icon {
  fill: #B5B5B5;
  opacity: 0.3;
}
.check__input:not(:checked) + .check__icon .check__icon-checkbox-on,
.check__input:not(:checked) + .check__icon .check__icon-radio-on {
  display: none;
}
.check__input:checked + .check__icon .check__icon-checkbox-off,
.check__input:checked + .check__icon .check__icon-radio-off {
  display: none;
}
.check--checkbox .check__input:checked + .check__icon {
  width: 27px;
}
