.feedback__description {
  margin-left: 0;
}
.feedback__item {
  text-align: right;
}
.feedback__link {
  color: #ffd180;
}
.feedback__link:hover {
  text-decoration: underline;
}
.feedback__link:active {
  text-decoration: none;
  color: #ee3643;
}
.feedback-form {
  display: grid;
  grid-template-rows: auto auto;
  row-gap: 37px;
}
.feedback-form__body {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 460px;
  column-gap: 20px;
  row-gap: 13px;
  background-color: #ffffff;
  margin-top: 38px;
  margin-right: auto;
  margin-left: auto;
}
.feedback-form__footer {
  display: flex;
  justify-content: center;
  background-color: #f4f4f4;
  padding-top: 37px;
  padding-bottom: 37px;
}
.feedback-form__label {
  font-size: 1.125em;
  line-height: 1.5rem;
  color: #000000;
}
.feedback-form__label--textarea {
  grid-column: 1 / -1;
}
.feedback-form__input,
.feedback-form__textarea {
  display: flex;
  flex-direction: column;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 400;
  font-size: 0.813em;
  line-height: 1.5rem;
  width: 100%;
  box-sizing: border-box;
  padding: 5px 10px;
  border-radius: 2px;
  border: 2px solid #c5c5c5;
  margin-top: 11px;
}
.feedback-form__textarea {
  padding-bottom: 10px;
  resize: none;
}
.feedback-sender-text {
  grid-column: 1 / 3;
}
.feedback-form__button {
  width: 460px;
}
.gallery {
  position: relative;
  background-color: #657E21;
}
.gallery__slide {
  position: relative;
}
.gallery__bar {
  position: absolute;
  color: #ffffff;
  top: 0;
  left: 24px;
}
.gallery__title {
  font-size: 2.25rem;
  line-height: 1;
  text-transform: uppercase;
  margin-top: 23px;
  margin-bottom: 5px;
}
.gallery__subtitle {
  font-size: 1.125rem;
  line-height: 24px;
  margin: 0;
}
.gallery__image {
  display: block;
}
.gallery__button {
  position: absolute;
  bottom: 27px;
  left: 24px;
  z-index: 10;
}
.gallery__slide--hidden {
  display: none;
}
.gallery__dot-controls {
  display: flex;
  justify-content: space-between;
  position: absolute;
  bottom: 36px;
  left: 50%;
  transform: translateX(-50%);
}
.gallery__dot {
  width: 10px;
  height: 10px;
  background-color: #ffffff;
  border: 2px solid #ffffff;
  border-radius: 50%;
  padding: 0;
  cursor: pointer;
}
.gallery__dot--current {
  background-color: #ee3643;
}
.gallery__dot:not(:last-child) {
  margin-right: 10px;
}
.gallery__arrow-controls {
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  top: 45%;
  padding: 0 24px;
}
.gallery__arrow {
  width: 22px;
  height: 40px;
