.modal {
  display: none;
  position: fixed;
  outline: none;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
  z-index: 100;
  border-radius: 4px;
}
.modal--show {
  display: block;
  animation: modalBounce 0.6s;
  background-color: #ffffff;
}
.modal--error {
  animation: modalShake 0.6s;
}
.modal__close {
  position: absolute;
  top: 14px;
  right: 11px;
  border-width: 0;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}
