  width: 620px;
  display: grid;
  grid-template-rows: auto auto;
}
.modal__cart-title {
  position: relative;
  display: block;
  font-size: 1.5em;
  line-height: 1.875rem;
  margin-top: 66px;
  margin-right: auto;
  margin-bottom: 68px;
  margin-left: auto;
}
.modal__icon-title {
  position: absolute;
  left: -95px;
  top: -18px;
}
