
.phone-link {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: @color-burnt-umber;
  padding-top: 6px;
  padding-right: 36px;
  padding-left: 36px;
  padding-bottom: 8px;
  border: 3px solid @color-burnt-umber;
  border-radius: 4px;
}

.phone-link__icon {
  fill: transparent;
  stroke: @color-burnt-umber;
  margin-right: 1rem;
}
