.features {
  margin-top: 59px;
  margin-bottom: 59px;
}

.features__list {
  display: grid;
  list-style-type: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px 20px;
}

.features__gallery {
  grid-column: ~"2 / -1";
  grid-row: ~"2 / 4";
}

.features__item {
  display: flex;
  flex-direction: column;
  background-repeat: no-repeat;
  padding-top: 20px;
  padding-bottom: 21px;
  padding-left: 22px;
  background-position-x: right;
  background-size: cover;

  &--fiction {
    background-image: url("../img/backgrounds/fiction.jpg");
  }

  &--business {
    background-image: url("../img/backgrounds/business.jpg");
  }

  &--gift-box {
    background-image: url("../img/backgrounds/gift-box.jpg");
  }

  &--esoterics {
    background-image: url("../img/backgrounds/esoterics.jpg");
  }

  &--delivery {
    background-image: url("../img/backgrounds/delivery.jpg");
  }
}

.features__title {
  font-size: 1.5em;
  line-height: 1.875rem;
  color: @color-white;
  margin-top: 0;
  margin-bottom: 14px;
  font-weight: 400;
}

.features__link {
  font-family: 'PT Sans', 'Arial', sans-serif;
  min-width: 135px;
  align-self: flex-start;
  font-size: 0.875em;
  line-height: 1.125rem;
  text-align: center;
  color: @color-white;
  background-color: @color-third-white;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 4px;
}

.features__link:hover {
  background-color: @color-third-silver;
}

.features__link:active {
  background-color: @color-third-dark-gray;
}
