.cart-list {
    display: none;
    position: absolute;
    z-index: 50;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 40px;
    padding-right: 40px;
    background-color: @color-pampas;
    top: 50px;
    border-style: solid;
    border-color: @color-black;
    border-width: 2px;
    box-sizing: border-box;
    width: 400px;
}

.cart-list--open {
    display: block;
}

.cart-list__title {
    margin: 0;
}


.cart-list__subtitle {
    font-family: "Inter", "Arial", sans-serif;
    margin-top: 20px;
    padding-bottom: 15px;
    border-bottom: 2px solid @color-dark-gray;
}


.cart-list__product {
    display: grid;
    grid-template-columns: min-content auto;
    border-bottom: 1px solid @color-dark-gray;
    padding-bottom: 8px;
    margin-bottom: 8px;
}

.cart-list__product:last-of-type {
    border: none;
    margin-bottom: 0;
}

.cart-list__product-data {
    margin-left: 20px;
    display: flex;
    justify-content: space-between;
}

.cart-list__controls {
    display: flex;
    flex-direction: column;
}

.cart-list__remove-button {
    border: none;
    background-color: transparent;
    cursor: pointer;
}

.cart-list__remove-button:hover {
    .cart-list__icon {
        stroke: @color-black;
    }
}

.cart-list__icon {
    margin-top: 8px;
    fill: transparent;
    stroke: @color-dark-gray;
}

.cart-list__product-title {
    margin-top: 0;
    margin-bottom: 0;
}

.cart-list__product-author {
    font-family: "Inter", "Arial", sans-serif;
    color: @color-silver;
    margin-top: 5px;
    margin-bottom: 0;
}

.cart-list__total {
    font-family: "Inter", "Arial", sans-serif;
    padding-top: 10px;
    border-top: 2px solid @color-silver;
    margin-bottom: 30px;
}

.cart-list__actions {
    display: grid;
    grid-template-rows: auto auto;
    row-gap: 10px;
}