.bar-link {
  display: flex;
  // align-items: flex-start;
  align-items: center;
  width: 150px;
  box-sizing: border-box;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 300;
  font-size: 1.125em;
  line-height: 1.5rem;
  text-align: center;
  color: @color-white;
  padding-top: 0.5em;
  padding-bottom: 0.5em;

  &--bookmark {
    padding-left: 20px;
  }

  &--cart {
    padding-left: 26px;
  }

  &--cart-added {
    background-color: @color-amaranth;
  }
}

.bar-link--place-order {
  padding-left: 19px;
  background-color: @color-apple;

  &:hover {
    background-color: @color-lima;
  }

  &:active {
    color: @color-half-white;
    background-color: @color-apple;
  }
}

.bar-link__icon {
  margin-right: 8px;
  opacity: 0.3;
  stroke: white;
  fill: transparent;
}

.bar-link:hover {
  .bar-link__icon {
    opacity: 1;
  }
}

.bar-link:active {
  background-color: @color-midnight-express;
  color: @color-half-white;

  .bar-link__icon {
    opacity: 0.3;
  }
}