.typography-list {
  font-size: 1.125em;
  line-height: 1.5rem;
  list-style-type: none;
  font-weight: 500;
  padding-left: 0;
  margin-top: 20px
}

.typography-list__item {
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 12px;
}

.typography-list__item::before {
  content: "";
  display: block;
  width: 4px;
  height: 4px;
  margin-right: 12px;
  background-color: @color-amaranth;
}
